import { Component, h, Event, Prop, EventEmitter } from '@stencil/core';
import { IVideo } from '../../types/Video';
import { EnumViews } from '../../models/EnumViews';

@Component({
    tag: 'video-thumb',
    shadow: true,
    styleUrl: 'video-thumb.scss',
})
export class VideoThumb {
    @Prop() public video: IVideo;
    @Event() setVideo: EventEmitter;
    @Event() changeView: EventEmitter;

    constructor() {
        this.handleSelectVideo = this.handleSelectVideo.bind(this);
        this.renderThumb = this.renderThumb.bind(this);
    }

    private getThumbUrl(originalUrl = '') {
        const path: string = originalUrl.replace(
            'https://video.getaccept.com/',
            ''
        );

        return `getaccept/video_thumb_proxy/${path}`;
    }

    public render() {
        return [
            <li class="video-thumb-container" onClick={this.handleSelectVideo}>
                {this.renderThumb(this.video.thumb_url)}
                <div class="video-title">{this.video.video_title}</div>
            </li>,
        ];
    }

    private renderThumb(originalUrl: string = '') {
        if (originalUrl.includes('vimeocdn')) {
            return (
                <div class="thumbnail">
                    <limel-icon name="vimeo" />
                </div>
            );
        }

        if (originalUrl.includes('ytimg')) {
            return (
                <div class="thumbnail youtube">
                    <limel-icon name="youtube_play" />
                </div>
            );
        }

        return <img class="thumbnail" src={this.getThumbUrl(originalUrl)} />;
    }

    private handleSelectVideo() {
        this.setVideo.emit(this.video.video_id);
        this.changeView.emit(EnumViews.sendDocument);
    }
}
