@import '../../gacolors.scss';

.video-thumb-container {
    display: flex;
    flex-direction: column;
    overflow: hidden;
    margin: 0.5rem;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.15);
    border-radius: 0.5rem;
    width: 10rem;
    height: 8rem;
    cursor: pointer;
    text-align: center;

    transform: scale(1);
    transition: 0.2s ease-in-out;
    &:hover {
        transform: scale(1.1);
        box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
    }

    .video-title {
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 0.8em;
        text-transform: uppercase;
        height: 100%;
        flex: 0;
    }

    .thumbnail {
        display: flex;
        flex: 2;
        justify-content: center;
        align-items: center;

        width: 100%;
        height: 6.5rem;
        object-fit: contain;

        &.youtube {
            color: $ga-red;
        }

        limel-icon {
            height: 4rem;
            width: 4rem;
        }
    }
}
